import Styles from './Header.module.scss';
import Container from '../ui/Container/Container';
import {useWindowSize} from "../../hooks/useWindowSize";
import {useState} from "react";
import classNames from "classnames";

const Header = ({ formBlockRef }) => {
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  const scrollToFormBlock = () => {
    formBlockRef.current.scrollIntoView({ behavior: 'smooth' });
  };
  const { mobileWidth,windowWidth } = useWindowSize();
  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);

  const toggleMobileNav = () => {
    setIsMobileNavOpen(!isMobileNavOpen);
  };


  return (
    <div className={Styles.Header}>
      <Container className={Styles.Container}>
        <div className={Styles.Wrapper}>
          <div className={Styles.Logo} onClick={scrollToTop}>
            <img className={Styles.LogoIcon} src={'img/logo/logo.svg'} alt='Logo icon' width={'100%'} height={'100%'} />
            <img className={Styles.LogoText} src={'img/logo/logo-text.svg'} alt='Logo text' />
          </div>
       <div className={Styles.navContainer}>
         {windowWidth < 345 ? null : <a className={Styles.Btn} onClick={scrollToFormBlock}>
           оставить заявку
         </a>}

             <button
                 className={classNames(Styles.Burger, isMobileNavOpen && Styles.Burger__active)} onClick={toggleMobileNav}
             >
               <span></span>
             </button>

       </div>
              <div className={classNames(Styles.BurgerMenu,!isMobileNavOpen ? Styles.BurgerMenuClose : null )}>
                <div className={Styles.BurgerMenuContent}>
                <div className={classNames(Styles.Logo,Styles.LogoBurger)} onClick={scrollToTop}>
                    {windowWidth < 440 ? <img className={Styles.LogoIcon} src={'img/logo/logo.svg'} alt='Logo icon' width={'26px'} height={'26px'} /> : <img className={Styles.LogoIcon} src={'img/logo/logo.svg'} alt='Logo icon' width={'44px'} height={'44px'} />}
                  <img className={Styles.LogoText} src={'img/logo/logo-text.svg'} alt='Logo text' />
                </div>

                  <a className={Styles.link} href="https://membrana.me">Membrana для физлиц</a>

                  {
                   windowWidth < 440 ?   <a className={classNames(Styles.Btn,Styles.btnBurger )} onClick={scrollToFormBlock}>
                      оставить заявку
                    </a> :  <a className={Styles.Btn} style={{width:"240px", textAlign:"center"}} onClick={scrollToFormBlock}>
                      оставить заявку
                    </a>
                  }
                </div>
              </div>

        </div>
      </Container>
    </div>
  );
};

export default Header;
